import "./App.css";
import PersonShow from "./components/PersonShow";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { Jeffbezos } from "./components/JeffBezos";
import { Billgates } from "./components/BillGates";
import MarkZuckerberg from "./components/MarkZuckerberg";
import BernardFamily from "./components/BernardFamily";

function App() {
  return (
    <Router>
      <div className="App">
        <Switch>
          <Route path="/BernardFamily">
            <BernardFamily />
          </Route>
          <Route path="/MarkZuckerberg">
            <MarkZuckerberg />
          </Route>
          <Route path="/Billgates">
            <Billgates />
          </Route>
          <Route path="/JeffBezos">
            <Jeffbezos />
          </Route>
          <Route path="/">
            <PersonShow />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
