import React from "react";
import { Link } from "react-router-dom";
import "./Person.css";

export default function Person(props) {
  const handleClick = () => {
    if (props.name === "Jeff Bezos") {
      window.location = "/JeffBezos";
    } else if (props.name === "Bill Gates") {
      window.location = "/Billgates";
    } else if (props.name === "Mark Zuckerberg") {
      window.location = "/MarkZuckerberg";
    } else if (props.name === "Bernard Arnault & family") {
      window.location = "/BernardFamily";
    }
  };
  return (
    <div className="person">
      <img
        className="person--image"
        onClick={handleClick}
        src={props.url}
        alt=""
      />
      <h1>{props.name}</h1>
      <p>{props.children}</p>
    </div>
  );
}
